@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400&display=swap);
html {
  scroll-behavior: smooth;
}
body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
code {
  /* font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace; */
  font-family: "Poppins", sans-serif;
}

@-webkit-keyframes cardUpDown {
  0% {
    -webkit-transform: translateZ(0px);
            transform: translateZ(0px);
  }
  50% {
    -webkit-transform: translateZ(10px);
            transform: translateZ(10px);
  }
  100% {
    -webkit-transform: translateZ(0px);
            transform: translateZ(0px);
  }
}

@keyframes cardUpDown {
  0% {
    -webkit-transform: translateZ(0px);
            transform: translateZ(0px);
  }
  50% {
    -webkit-transform: translateZ(10px);
            transform: translateZ(10px);
  }
  100% {
    -webkit-transform: translateZ(0px);
            transform: translateZ(0px);
  }
}
@-webkit-keyframes cardLeftRight {
  0% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  50% {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
  }
  100% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}
@keyframes cardLeftRight {
  0% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  50% {
    -webkit-transform: translateX(10px);
            transform: translateX(10px);
  }
  100% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
}
a {
  text-decoration: none;
  color: inherit;
}

